<template>
    <section class="login">
        <div class="form" @keyup.enter="login">
            <p>{{state==='login'?'User Login':'Register'}}</p>
            <p>
                <span>User Name</span>
                <br>
                <input id="userName"
                       type="text"
                       name="userName"
                       @keyup="inputFilter"
                       v-model="userName">
                <label for="userName"></label>
                <br>
                <span>Password</span>
                <br>
                <input id="password"
                       type="password"
                       v-model="password">
                <label for="password"></label>
                <br>
                <span v-show="state!=='login'?true:false">
                    Re-enter password
                </span>
                <br v-show="state!=='login'?true:false">
                <input id="rePassword"
                       type="password"
                       v-show="state!=='login'?true:false"
                       v-model="rePassword">
                <label for="rePassword"
                       v-show="state!=='login'?true:false">
                </label>
            </p>
            <p>
                <button @click="temp"
                        v-show="state==='login'?true:false">Login
                </button>
                <button @click="submitRegister"
                        v-else>Register
                </button>
            </p>
            <p><span>No Account? Click</span>
                <button @click="register">Here</button>
                <span>To Register</span>
            </p>
        </div>
    </section>
</template>
<script>
    export default{

        data(){
            return {
                userName: 'a'
                , password: 'b'
                , rePassword: ''
                , state: 'login'
            }
        }
        , methods: {
            login: function () {
                if (this.state !== 'register') {
                    if (users[this.userName]) {
                        if (users[this.userName.trim()]['password'] === this.password) {
                            setCookie(this.userName.trim())
                            window.location.href = './pages/list.html'
                        } else {
                            alert('Incorrect Password')
                        }
                    } else {
                        alert('Invalid Account')
                    }
                }
            }
            , register: function () {
                this.state = this.state === 'login' ? 'register' : 'login'
                this.userName = ''
                this.password = ''
                this.rePassword = ''
            }
            , submitRegister: function () {
                if (this.userName.trim() && this.password.trim()) {
                    if (this.password === this.rePassword) {
                        setCookie(this.userName.trim())
                        users[this.userName] = {
                            password: this.password
                            , qns: []
                        }
                        localStorage.users = JSON.stringify(users)
                        window.location.href = './pages/list.html'
                    } else {
                        alert('Different Password Input')
                    }
                } else {
                    alert('Please Input Valid UserName And Password')
                }
            }
            , inputFilter: function () {
                this.userName = this.userName.replace(/[^\d|A-z]/g, '')
            }
            , temp: function () {
                this.$http.post('/someUrl',{foo:'bar'})
                        .then((response)=>{
                            console.log(response.status)
                            console.log(response.statusText)
                            console.log(response.headers)
                            console.log(response.headers['Expires'])
                        },(response)=>{
                            console.log('er')
                        })
            }
        }
    }
</script>
<style lang="sass">
    @import "../SCSS/Login.scss";
</style>


